.groom{
    height: 100vh;
    background-color: white;
    width: 100%;
    position: relative;
}
.groom_bg{
    width: 70%;
    height: 80%;
    background-color: #E2D1F9;
    position: absolute;
    right: 30px;
    bottom: 30px;
}
.groom_bg_s{
    width: 250px;
    height: 250px;
    position: absolute;
    top: 100px;
    left: 30px;
    background-position: center;
    background-size: cover;

}
.groom_image{
    width: 260px;
    height: 260px;
    position: absolute;
    top: 60px;
    left: 60px;
    background-position: center;
    background-size: cover;
}
.groom_text{
    width: 90%;
    height: 45%;
    position: absolute;
    margin: auto;
    right: 0;
    left: 0;
    bottom: 30px;
}

.groom_text h1{
    text-align: center;
    font-size: 50px;
    font-family: Raleway;
    font-weight: 300;
    letter-spacing: 2px;
    color: #600f44;
}

.groom_text h2{
    font-family: Dancing Script;
    font-size: 50px;
    text-align: center;
    color: #600f44;
    margin-top: 10px;
}

.groom_text h3{
    font-size: 15px;
    font-weight: 500;
    line-height: 1.6;
    text-align: center;
    margin: 30px;
}

