.proposal{
    margin: 40px 20px 0px 0px !important;
    display: flex;
    flex-direction: row;
    background-size: cover;
    background-position: center;
    border-radius: 0px 100px 100px 0;
}
.proposal_bg{
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.775);
    border-radius: 0px 100px 100px 0;
}
.proposal_text{
    flex: 2;
}
.proposal_text h2{
    margin-top: 20px;
    text-align: center;
    font-family: Dancing Script;
    font-size: 70px;
    color: #600f44;
}
.proposal_text p{
    text-align: center;
    font-size: 15px;
    font-weight: 300;
    margin: 10px 20px 0 20px;
}
.proposal_image{
    flex: 1;
    text-align: center;
}


@media screen and (max-width: 600px){
    .proposal{
        flex-direction: column;
    }
    .proposal_text{
        flex: 1;
    }
    .proposal_text h2{
        margin-top: 20px;
        text-align: center;
        font-family: Dancing Script;
        font-size: 60px;
    }
    .proposal_text p{
        text-align: justify;
        font-size: 15px;
        font-weight: 300;
        margin: 10px 20px 20px 20px;
    }
}